body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem;
  min-height: 100vh;
}
.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dark-mode{
  background-color: black;
}
.dark-mode h1{
  color: white;
}

.notes-list{
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.note.new{
  background-color: rgb(12, 201, 201);
}
textarea{
  border: none;
  outline: none;
  resize: none;
  background: transparent;
}
.red{
  color: red;
}
.save{
  background-color: rgb(225, 221, 221);
  border: none;
  border-radius: 1rem;
  padding: 5px 10px;
  cursor: pointer;
}
.save:hover{
  background-color: rgb(245, 243, 243);
}

.note{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 170px;
  background-color: #fef68a;
  border-radius: .5rem;
  padding: 1rem;
  white-space: pre-wrap;
}
.note-footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.delete-icon{
  cursor: pointer;
}
.search{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: .25rem;
  background-color: rgb(230, 230, 230);
  border: none;
  border-radius: .5rem;
}
.search input{
  width: 100%;
  padding: .35rem;
  border: none;
  outline: none;
  background: transparent;
}
